@font-face {
  font-family: "Forum";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Forum/forum.woff2") format("woff2"), url("../fonts/Forum/forum.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "GTWalsheimPro";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/GTWalsheimPro/GTWalsheimPro-Regular.woff2") format("woff2"), url("../fonts/GTWalsheimPro/GTWalsheimPro-Regular.woff");
  font-display: swap;
}
@font-face {
  font-family: "GTWalsheimPro";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/GTWalsheimPro/GTWalsheimPro-Medium.woff2") format("woff2"), url("../fonts/GTWalsheimPro/GTWalsheimPro-Medium.woff");
  font-display: swap;
}
img {
  max-width: 100%;
  object-fit: cover;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-decoration: none;
  text-align: center;
  color: #ffffff;
  background-color: #474442;
  border: none;
  cursor: pointer;
  transition: color 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95), background-color 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95), opacity 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95);
}
.button:hover,
.button:focus {
  background-color: #1a1716;
  outline: none;
}
.button:active {
  color: #acacac;
  background-color: #5f5f5f;
}
.page {
  height: 100%;
}
.page__body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: #474442;
  background-color: #ffffff;
}
.hero {
  background-color: #9f948f;
}
.hero__wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 375px;
  padding-top: 48px;
  padding-bottom: 48px;
  text-align: center;
}
@media (min-width: 1280px) {
  .hero__wrapper {
    width: 1280px;
    padding: 40px 0 48.5px 0;
    box-sizing: initial;
  }
}
.hero__title {
  margin: 0;
  font-family: "Forum", "Arial", serif;
  font-weight: 400;
  font-size: 48px;
  line-height: 48px;
  letter-spacing: -0.02em;
  color: #ffffff;
}
@media (min-width: 1280px) {
  .hero__title {
    font-size: 64px;
    line-height: 64px;
  }
}
.hero__breadcrumbs {
  display: none;
}
@media (min-width: 1280px) {
  .hero__breadcrumbs {
    display: block;
  }
}
.cart__wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 375px;
  padding-top: 16px;
  padding-bottom: 48px;
  display: grid;
  text-align: center;
}
@media (min-width: 1280px) {
  .cart__wrapper {
    width: 1280px;
    padding: 140px 48.5px 108px 48.5px;
  }
}
.cart__wrapper--empty-cart {
  padding-top: 80px;
}
.cart__image {
  margin-bottom: 30px;
}
@media (min-width: 1280px) {
  .cart__image {
    margin-bottom: 48px;
  }
}
.cart__icon {
  width: 87px;
  height: 106px;
}
@media (min-width: 1280px) {
  .cart__icon {
    width: 113px;
    height: 138px;
  }
}
.cart__info {
  margin: 0 0 24px 0;
  font-family: "Forum", "Arial", serif;
  font-weight: 400;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: -0.02em;
  color: #474442;
  max-width: 283px;
  justify-self: center;
}
@media (min-width: 1280px) {
  .cart__info {
    font-size: 64px;
    line-height: 64px;
    max-width: 100%;
  }
}
.cart__description {
  margin: 0 0 34px 0;
  padding: 0 15px;
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #5f5f5f;
}
@media (min-width: 1280px) {
  .cart__description {
    margin-bottom: 48px;
    font-size: 20px;
    line-height: 24px;
  }
}
.cart__buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}
@media (min-width: 1280px) {
  .cart__buttons {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }
}
.cart__button {
  width: 100%;
  padding: 18px 0;
}
@media (min-width: 1280px) {
  .cart__button {
    width: 220px;
  }
}
.cart__button--light {
  color: #474442;
  background-color: #ffffff;
  border: 1px solid #474442;
}
.cart__button--light:hover,
.cart__button--light:focus {
  background-color: #f5f4f2;
}
.cart__button--light:active {
  border: 1px solid #acacac;
  color: #acacac;
  background-color: transparent;
}
.form__group {
  margin: 0;
  padding: 0;
  border: none;
}
.form__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}
.form__item {
  position: relative;
  padding: 32px 0;
  display: grid;
  grid-template-columns: 80px 96px 111px 48px;
  grid-template-rows: repeat(3, auto);
  row-gap: 10px;
}
@media (min-width: 1280px) {
  .form__item {
    border: 1px solid #e0deda;
    grid-template-columns: 120px 485px 486px auto;
    grid-template-rows: auto;
    row-gap: 0;
    padding: 0;
  }
}
.form__item:not(:last-child)::after,
.form__item:not(:last-child)::before {
  content: "";
  position: absolute;
  display: block;
  width: 400%;
  height: 1px;
  background: #ebebeb;
  bottom: 0;
  right: 0;
}
@media (min-width: 1280px) {
  .form__item:not(:last-child)::after,
  .form__item:not(:last-child)::before {
    display: none;
  }
}
.form__item:not(:last-child)::after {
  left: 0;
}
@media (min-width: 1280px) {
  .form__item:not(:last-child)::after {
    display: none;
  }
}
.form__image-wrapper {
  grid-column: 1/2;
  grid-row: 1/2;
  display: grid;
  height: 80px;
  align-items: center;
  justify-content: center;
  border: 1px solid #e0deda;
  background-color: #f5f4f2;
}
@media (min-width: 1280px) {
  .form__image-wrapper {
    height: 130px;
    border-top: 0;
    border-bottom: 0;
    border-left: 0;
  }
}
.form__image img {
  display: block;
}
.form__image--bag {
  width: 52px;
  height: 42px;
}
@media (min-width: 1280px) {
  .form__image--bag {
    width: 79px;
    height: 70px;
  }
}
.form__image--black-shoes {
  width: 96px;
  height: 87px;
}
@media (min-width: 1280px) {
  .form__image--black-shoes {
    width: 120px;
    height: 130px;
  }
}
.form__image--white-shoes {
  width: 61px;
  height: 45px;
}
@media (min-width: 1280px) {
  .form__image--white-shoes {
    width: 91px;
    height: 73px;
  }
}
.form__product-name {
  margin: 0;
  padding-left: 20px;
  grid-column: 2/4;
  grid-row: 1/2;
  width: 187px;
  text-decoration: none;
  text-align: left;
  transition: color 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95), opacity 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95);
}
@media (min-width: 1280px) {
  .form__product-name {
    padding-left: 24px;
    grid-column: 2/3;
    align-self: center;
    width: 336px;
  }
}
.form__product-name:hover {
  opacity: 0.5;
}
.form__title {
  margin-top: 0;
  margin-bottom: 4px;
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #474442;
}
@media (min-width: 1280px) {
  .form__title {
    font-size: 20px;
    line-height: 24px;
  }
}
.form__description {
  margin: 0;
  max-width: 153px;
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #acacac;
}
@media (min-width: 1280px) {
  .form__description {
    max-width: 336px;
  }
}
.form__product-button {
  position: relative;
  width: 48px;
  min-height: 80px;
  background-color: #ffffff;
  border: 1px solid #e0deda;
  grid-column: 4/5;
  grid-row: 1/2;
}
.form__product-button::before,
.form__product-button::after {
  content: "";
  position: absolute;
  width: 13px;
  height: 2px;
  left: 17px;
  top: 36px;
  background-color: #474442;
  transform: rotate(45deg);
}
.form__product-button::after {
  transform: rotate(-45deg);
}
@media (min-width: 1280px) {
  .form__product-button {
    grid-column: 4/5;
    width: 90px;
    min-height: 130px;
    border-top: 0;
    border-bottom: 0;
    border-right: 0;
  }
  .form__product-button::before,
  .form__product-button::after {
    top: 60px;
    left: 35px;
  }
}
.form__product-button:hover,
.form__product-button:focus {
  background-color: #f5f4f2;
  outline: none;
}
.form__product-button:active {
  background-color: #f5f4f2;
  opacity: 0.5;
}
.form__info-names {
  padding-top: 6px;
  grid-column: 1/5;
  grid-row: 2/3;
  display: grid;
  grid-template-columns: 80px 106px 109px;
  gap: 20px;
  text-align: left;
}
@media (min-width: 1280px) {
  .form__info-names {
    display: none;
  }
}
.form__info-text {
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  color: #5f5f5f;
}
.form__info-names--head {
  display: none;
}
@media (min-width: 1280px) {
  .form__info-names--head {
    display: grid;
    grid-template-columns: 559px 132px 102px auto;
    gap: 44px;
    margin-bottom: 16px;
  }
}
.form__info-prices {
  grid-column: 1/5;
  grid-row: 3/4;
  display: grid;
  grid-template-columns: 80px 106px 109px;
  gap: 20px;
  text-align: left;
}
@media (min-width: 1280px) {
  .form__info-prices {
    grid-template-columns: 129px 100px auto;
    gap: 45px;
    grid-column: 3/4;
    grid-row: 1/2;
  }
}
.form__field,
.form__info-price {
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #474442;
}
.form__field {
  width: 56px;
  height: 40px;
  padding: 9px 5px 9px 16px;
  border: none;
  outline: 1px solid #e0deda;
  appearance: textfield;
}
@media (min-width: 1280px) {
  .form__field {
    height: 46px;
    align-self: center;
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
.form__info-price {
  margin: 0;
  align-self: center;
}
@media (min-width: 1280px) {
  .form__info-price {
    font-size: 20px;
    line-height: 24px;
  }
}
.form__info-price-value,
.form__order-price-value {
  color: #acacac;
}
.form__group--promo {
  position: relative;
  padding-top: 24px;
  padding-bottom: 24px;
  display: flex;
}
.form__group--promo::after,
.form__group--promo::before {
  position: absolute;
  content: "";
  width: 800%;
  height: 95px;
  background-color: #f5f4f2;
  bottom: 0;
  right: 0;
  z-index: -1;
}
.form__group--promo::after {
  left: 0;
}
@media (min-width: 1280px) {
  .form__group--promo {
    padding: 41px 0 41px 40px;
    border: 1px solid #e0deda;
    border-top: none;
  }
  .form__group--promo::after,
  .form__group--promo::before {
    display: none;
  }
}
.form__group--promo .form__field {
  margin-right: 16px;
  padding: 16px;
  width: 260px;
  font-size: 14px;
  line-height: 16px;
  color: #474442;
}
@media (min-width: 1280px) {
  .form__group--promo .form__field {
    margin-right: 0;
    width: 338px;
  }
}
.form__group--promo .form__field::placeholder {
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #474442;
}
.form__group--promo .form__field:focus {
  outline: 2px solid #474442;
}
.form__promo-button {
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  border: none;
  text-align: left;
  text-decoration: underline;
  color: #474442;
  background-color: transparent;
}
@media (min-width: 1280px) {
  .form__promo-button {
    padding: 15px 27px;
    text-decoration: none;
  }
}
.form__promo-button:hover {
  opacity: 0.5;
  background-color: transparent;
}
.form__promo-button:focus {
  outline: auto;
  background-color: transparent;
}
.form__promo-button:active {
  background-color: transparent;
}
.form__order {
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1280px) {
  .form__order {
    padding-top: 48px;
  }
}
.form__order-total {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #5f5f5f;
}
.form__order-price {
  margin: 10px 0 28px 0;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  color: #474442;
}
@media (min-width: 1280px) {
  .form__order-price {
    font-size: 32px;
    line-height: 38px;
    margin-bottom: 14px;
  }
}
.form__order-button {
  width: 280px;
  padding: 18px 0;
}
.form__error .form__field {
  outline: 2px solid #e07c66;
}
@media (min-width: 1280px) {
  .breadcrumbs {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 33px;
    margin-bottom: 8px;
  }
}
@media (min-width: 1280px) {
  .breadcrumbs__item {
    padding: 0;
    position: relative;
  }
}
@media (min-width: 1280px) {
  .breadcrumbs__item:not(:last-child)::after {
    position: absolute;
    content: "";
    width: 1px;
    height: 16px;
    left: 70px;
    background-color: #ffffff;
    opacity: 0.4;
  }
}
.breadcrumbs__link {
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  text-decoration: none;
  transition: color 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95), opacity 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95);
}
.breadcrumbs__item:not(:last-child) .breadcrumbs__link {
  opacity: 0.6;
}
.breadcrumbs__item:not(:last-child) .breadcrumbs__link:hover,
.breadcrumbs__item:not(:last-child) .breadcrumbs__link:focus {
  color: #acacac;
}
.breadcrumbs__item:not(:last-child) .breadcrumbs__link:active {
  opacity: 0.5;
}
.sitemap__wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 375px;
  padding-top: 16px;
  padding-bottom: 48px;
  display: grid;
}
@media (min-width: 1280px) {
  .sitemap__wrapper {
    width: 1280px;
    padding-top: 48px;
  }
}
.sitemap__title {
  margin-top: 0;
  margin-bottom: 10px;
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #474442;
}
@media (min-width: 1280px) {
  .sitemap__title {
    font-size: 20px;
    line-height: 24px;
  }
}
.sitemap__list {
  margin: 0;
  padding: 0;
  list-style-position: inside;
}
.sitemap__item {
  margin-left: 15px;
  margin-bottom: 10px;
}
.sitemap__link {
  margin: 0;
  padding: 5px 0 5px 0;
  font-family: "GTWalsheimPro", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #474442;
  text-decoration: none;
  transition: color 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95), opacity 0.3s cubic-bezier(0.45, 0.05, 0.55, 0.95);
}
@media (min-width: 1280px) {
  .sitemap__link {
    font-size: 16px;
    line-height: 18px;
  }
}
.sitemap__link:hover,
.sitemap__link:focus {
  color: #acacac;
}
.sitemap__link:active {
  opacity: 0.5;
}
